
.center-column {
  padding:40px;
  max-height:100%; /* seems required for firefox to appropriately scale tall hackmap images */
}
.droptarget {
  width:90%;
  max-width:600px;
  position:relative;
  margin:0 auto 0 auto;
}
.floorplan {
  /* floorplan image is assumed to be black vector image */
  -webkit-filter:invert(100%) opacity(40%);
          filter:invert(100%) opacity(40%);
  z-index:0;
  width:100%;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
}
.marker {
  position:absolute;
  z-index:1;
  -webkit-transition:-webkit-box-shadow 100ms ease-in-out;
  transition:-webkit-box-shadow 100ms ease-in-out;
  transition:box-shadow 100ms ease-in-out;
  transition: box-shadow 100ms ease-in-out, -webkit-box-shadow 100ms ease-in-out;
  -webkit-box-shadow: 0 0 0 0 #3a7be2;
          box-shadow: 0 0 0 0 #3a7be2;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
}
.marker.selected {
  -webkit-animation: pulse 0.8s 3;
          animation: pulse 0.8s 3;
  -webkit-box-shadow: 0 0 5px 3px #3a7be2;
          box-shadow: 0 0 5px 3px #3a7be2;
}
@-webkit-keyframes pulse {
from {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
50% {
    -webkit-transform: scale3d(1.2, 1.2, 1.2);
            transform: scale3d(1.2, 1.2, 1.2);
}
to {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
}
@keyframes pulse {
from {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
50% {
    -webkit-transform: scale3d(1.2, 1.2, 1.2);
            transform: scale3d(1.2, 1.2, 1.2);
}
to {
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
}
@-webkit-keyframes bounce {
0%, 20%, 50%, 80%, 100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
40% {
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
}
60% {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
}
}
@keyframes bounce {
0%, 20%, 50%, 80%, 100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
40% {
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
}
60% {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
}
}



